.faq {
    width: 1600px;
    max-width: 60%;
    margin-left: auto;
    margin-right: auto;
    overflow-x: hidden;
    margin-bottom: 8px;
}

.faq-item h3 {
    color: black;
}

.faq-item div {
    background-color: rgb(225, 223, 223);
    color: black;
    border-radius: 5px;
    padding: 8px;
}

.buttonfaq {

    /* Ensure inputs and buttons take full width */
    padding: 10px;
    margin: 5px 0;
    box-sizing: border-box;
    border: none;
    color: white;
    background-color: rgb(0, 0, 0);
    border-radius: 8px;
}

.buttonfaq:hover {

    /* Ensure inputs and buttons take full width */
    padding: 10px;
    margin: 5px 0;
    box-sizing: border-box;
    border: none;
    color: rgb(0, 0, 0);
    background-color: rgb(100, 138, 208);
    cursor: pointer;
}


@media (min-width:375px) and (max-width:475px) {
    .faq {
        max-width: 90%;
        background-color: #ffffff;
    }

}