/* === General Reset & Wrappers === */
.home {
    width: 100%;
    height: 100vh;
    /* make sure it fills the screen height */
    box-sizing: border-box;
    background-image: url("/src/components/images/PunakhaDzong.jpg");
    background-size: cover;
    background-position: bottom;
    background-repeat: no-repeat;
    filter: contrast(95%) brightness(1.1);
}

.banner {
    width: 100%;
    display: block;
    box-sizing: border-box;
}

.home-info {
    width: 100%;
    margin: 0 auto;
    padding: 1rem;
    box-sizing: border-box;
}

/* === Layout Container Below Banner === */
.info-container {
    display: flex;
    flex-wrap: wrap;
    /* Allow wrapping on zoom/small screens */
    justify-content: space-between;
    gap: 1rem;
    width: 100%;
    box-sizing: border-box;
}

/* === Welcome Text Section (Left Box) === */
.welcome-text {
    flex: 1 1 300px;
    min-width: 300px;
    background-color: transparent;
    border-radius: 16px;
    font-family: inherit;
}

.welcome-heading {
    font-size: 1.5rem;
    background-color: rgb(252, 252, 252);
    color: #313189;

    border-radius: 8px 8px 0 0;
    font-weight: 600;
    text-align: left;
}

.welcome-subheading {
    color: #272525;
    font-size: 1.2rem;
}

.welcome-text-inner {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 100%;
    height: auto;
    padding: 0.5rem;
    box-sizing: border-box;
}

/* === Buttons and Widgets in Welcome Box === */
.inner-widget-base {
    display: flex;
    padding: 0.4rem 0.7rem;
    border-radius: 6px;
    white-space: nowrap;
    text-decoration: underline;
    text-underline-offset: 3px;
    font-weight: 600;
    cursor: pointer;
    color: #000000;
    background: transparent;
    transition: background 0.2s ease;
}

.inner-widget-base:hover {
    transition: all 0.3s ease;
    color: rgb(148, 121, 0);
}

.trip-planner-button {
    height: 5vh;
    padding: 3px 15px;
    width: 95%;
    margin: auto;
    box-sizing: border-box;
    border-radius: 8px;
    box-shadow: 0 6px 16px rgb(166, 159, 159);
    cursor: pointer;
    text-align: center;
    animation: changeColor 8s ease-in-out infinite alternate;
}

@keyframes changeColor {

    0% {
        background-color: rgb(0, 0, 0, 0.8);
        color: rgb(255, 255, 255);
    }

    50% {
        background-color: rgb(0, 0, 0);
        color: rgb(20, 201, 0);
    }

    100% {
        background-color: rgb(0, 0, 0, 0.8);
        color: rgb(255, 254, 254);
    }
}

/* === Right Side Gallery Section === */
.welcome-text-1 {
    flex: 1 1 200px;
    min-width: 400px;
    background-color: transparent;
    border-radius: 16px;
    font-family: inherit;
}

.welcome-heading-1 {
    font-size: 1rem;
    /* background: linear-gradient(135deg, #e4ea33, #eb8332); */
    background-color: rgb(94, 84, 84);
    color: rgb(255, 236, 236);
    padding: 0.5rem 1rem;
    border-radius: 6px 6px 0 0;
    font-weight: 600;
    text-align: center;
}

.gallery-container {
    flex: 1 1 300px;
    min-width: 300px;
    display: flex;
    justify-content: center;
    background-size: cover;
}

.service-widget {
    /* box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); */
    padding: 20px;
    text-align: center;
    color: rgb(0, 0, 0);
    border-radius: 8px;
    background-color: rgba(0, 0, 0, 0.4);
    margin: 5px;
    align-items: center;
}

.service-header {
    font-size: 1.2rem;
    padding: 5px;
    color: #ffffff;
    font-weight: 600;
    margin-bottom: 3px;
    background-color: black;
    width: 35%;
    margin: 0 auto 2px auto;
}

.services-column {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 0 0;
}

.service-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    border-radius: 12px;
    gap: 1rem;
    cursor: pointer;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    background-color: rgb(230, 234, 241, 0.8);
    opacity: 0.95;
}

.service-box:hover {
    transform: translateY(-6px) scale(1.03);
    box-shadow: 0 10px 18px rgba(0, 0, 0, 0.25);
    background: rgb(234, 195, 168);
}

.service-content {
    display: flex;
    flex-direction: column;
    text-align: left;
    flex: 1;
    margin-left: 1.5rem;
}

.service-title {
    font-weight: bold;
    font-size: 1.1rem;
    margin-bottom: 4px;
}

.service-description {
    font-size: 0.9rem;
    color: #333;
}

.service-arrow {
    font-size: 1.5rem;
    margin-left: 1rem;
    color: #313189;
}

.service-icon {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #f0f0f0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 1rem;
    color: #313189;
}

/* === Responsive Fixes === */
@media (max-width: 768px) {
    .info-container {
        flex-direction: column;
    }

    .welcome-text,
    .welcome-text-1,
    .gallery-container {
        width: 100%;
        flex: 1 1 100%;
    }

    .welcome-text-inner {
        flex-direction: column;
        padding: 0;
    }

    .inner-widget-base {
        width: 100%;
    }
}