.footer {
    background-color: #1f2937;
    /* dark slate gray */
    color: #f9fafb;
    /* off-white */
    padding: 2rem 1rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.footer-inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 1100px;
    margin: 0 auto;
    gap: 2rem;
}

.footer-section {
    flex: 1 1 200px;
    min-width: 180px;
}

.footer-title {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
    font-weight: 700;
    color: #fbbf24;
    /* amber-400 */
}

.footer-heading {
    font-size: 1.2rem;
    margin-bottom: 0.75rem;
    font-weight: 600;
    color: #fbbf24;
}

.footer-links {
    list-style: none;
    padding: 0;
    margin: 0;
}

.footer-links li {
    margin-bottom: 0.5rem;
}

.footer-links li a {
    color: #e0e7ff;
    /* light indigo */
    text-decoration: none;
    transition: color 0.3s ease;
}

.footer-links li a:hover {
    color: #fbbf24;
    text-decoration: underline;
}

.footer-section p {
    margin: 0.25rem 0;
    line-height: 1.4;
}

.footer-bottom {
    text-align: center;
    margin-top: 2rem;
    font-size: 0.9rem;
    color: #94a3b8;
    /* slate-400 */
    border-top: 1px solid #374151;
    /* gray-700 */
    padding-top: 1rem;
}

/* Responsive: stack vertically on smaller screens */
@media (max-width: 600px) {
    .footer-inner {
        flex-direction: column;
        gap: 1.5rem;
    }

    .footer-section {
        min-width: 100%;
    }
}